
          @import "src/styles/_variables";
          @import "src/styles/_mixins";
        
.trending-toolbar {
  display: flex;
  justify-content: space-between;
  background-color: var(--card-background-color);
  color: var(--text-color);
  padding: 5px 20px;
  overflow: hidden;
  border-bottom: 1px solid var(--border-color);
  overflow: visible;
  position: sticky;
  top: 70px;
  z-index: 3;

  .scroll-container {
    display: flex;
    align-items: center;
    animation: scroll 180s linear infinite;
  }
  
  .stock-item {
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
      margin-left:5px;
    }
  }

  .stock-item, .index-item {
    margin: 0 7px;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: #444;

    }
  }
  
  .stock-percent{
    margin-left:5px;
  }

  .stock-price{
    margin-left:5px;
  }

  .stock-symbol {
    color: var(--text-color);
    font-weight: 600;
  }

  .index-data {
    display: flex;
    align-items: center;
    margin-right: 20px;
    width: 650px;
  }
  
  .index-item {
    display: flex;
    justify-content: center;
    align-items: center;
  
    svg {
      margin: 0 5px;
    }
  }

  .trending-stocks {
    display: flex;
    align-items: center;
    white-space: nowrap;
    overflow: hidden;
    position: relative;
    width: 100%;
  }
  
  .trending-label {
    text-transform: uppercase;
    font-size: 0.6rem;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
      margin-left: 5px;
    }
  }

  .positive {
    color: var(--success-color);
  }
  
  .negative {
    color: var(--danger-color);
  }
  .form-check.form-switch .form-check-input {
    align-content: center;
    display: block;
    margin: auto;
    text-align: center;
  }
  
  .form-check.form-switch .form-check-label {
    display: block;
  }

  .form-check.form-switch {
    margin: 0;
    min-height: 0;
    display: flex;
    padding: 0;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  /* Stop scrolling on hover */
  .trending-stocks:hover .scroll-container {
    animation-play-state: paused;
  }
}



@keyframes scroll {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-50%);
  }
}



